import React,{Component} from 'react';
import ReactDom from 'react-dom';
import './styles.css';

let show = false;
export default class RenderInBody extends Component{
    constructor(props){
        super(props);
        this.onClick = this.onClick.bind(this);
    }
    componentDidMount(){
        this.renderLayer();
    }
    onClick(e){
        if(show&&e.target !== this.popup.firstChild){
            this.close();
        }
    }
    componentDidUpdate() {
        this.renderLayer();
    }
    close(){
        if(!show) return;
        show = false;
        ReactDom.unmountComponentAtNode(this.popup);
        document.body.removeChild(this.popup);
        document.removeEventListener('click',this.onClick);
        this.popup = null;
    }
    componentWillUnmount(){
        this.close();
    }
    renderLayer(){
        this.popup = document.createElement("div");
        this.popup.className = 'center';
        document.body.appendChild(this.popup);
        document.addEventListener('click',this.onClick);
        ReactDom.render(this.props.children, this.popup,()=>{
            this.popup.style.marginLeft = -this.popup.offsetWidth/2;
            this.popup.style.marginTop = -this.popup.offsetHeight/2;
            show = true;
        });
    }
    render(){
        return null;
    }
}